@mixin makeClockCenter {
  height: $clock-center-size;
  width: $clock-center-size;
  border-radius: 50%;
}
@mixin makeClock {
  height: $clock-size;
  width: $clock-size;
  border-radius: 50%;
}

@mixin setBackground {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
